<template>
    <div class="container">
        <van-actionsheet
                v-model="show"
                :actions="actions"
                cancel-text=""
                @select="onSelect"
                @cancel="onCancel"
        />

        <van-button @click="openSelect" class="ok"></van-button>
    </div>
</template>
<script>
    import wx from 'weixin-js-sdk'
    import {SIGN_PACKAGE, BIND, DRIVER_VERIFY, FETCH_OPENID} from '../../common/config'
    import {get, post} from '../../common/http'
    import store from '../../storage'
    import {Toast} from 'vant';
    import {Actionsheet, Button} from 'vant';

    export default {
        components: {
            [Actionsheet.name]: Actionsheet,
            [Button.name]: Button
        },
        data() {
            return {
                data: {},
                position: 0,
                show: false,
                actions: [
                    {
                        name: '',
                        position: 1
                    },
                    {
                        name: '',
                        position: 2
                    },
                    {
                        name: '()',
                        position: 3
                    },
                    {
                        name: '()',
                        position: 4
                    }
                ]

            }
        },
        methods: {
            openSelect() {
                this.show = true
            },
            onSelect(item) {
                this.show = false
                this.position = item.position
                this.scan()
            },
            onCancel() {
                this.show = false
            },
            initConfig() {
                get(SIGN_PACKAGE, {}, res => {
                    wx.config({
                        debug: true,
                        appId: res.appId,
                        timestamp: "" + res.timestamp,
                        nonceStr: res.nonceStr,
                        signature: res.signature,
                        jsApiList: ['checkJsApi', 'scanQRCode']
                    });
                })
            },
            scan() {
                wx.checkJsApi({
                    jsApiList: ['scanQRCode'],
                    error: function () {
                        Toast.fail("")
                    }
                });
                wx.scanQRCode({
                    needResult: 1,
                    scanType: ["qrCode"],
                    success: function (res) {
                        const mac = res.resultStr
                        const position = this.position
                        const openid = this.data.openid
                        post(BIND, {mac, position, openid}, () => {
                            Toast.success("")
                        })
                    }
                });
            }
        }
        ,
        created() {
            const data = store.fetch()
            this.initConfig()
            if (data && data.hasOwnProperty("openid")) {
                // has open id?
                this.data = data
                if (data.hasOwnProperty("id")) {
                    // init config.
                } else {
                    const params = {openId: data.openid}
                    // is bind?
                    post(DRIVER_VERIFY, params, res => {
                            let resData = res;
                            resData.openid = this.data.openid
                            store.save(resData)
                            this.data = resData
                        }, () => this.$router.push({name: 'bind'})
                    );
                }
            } else {
                const code = store.urlParam('code')
                if (code) {
                    const params = {code}
                    get(FETCH_OPENID, params, res => {
                        this.data.openid = res.openid;
                        store.save(this.data)
                        let param = {openId: this.data.openid}
                        post(DRIVER_VERIFY, param, res => {
                                let resData = res;
                                resData.openid = this.data.openid
                                store.save(resData)
                                this.initConfig()
                            }, () => this.$router.push({name: 'bind'})
                        );
                    })
                    ;
                } else {
                    Toast.fail("")
                }
            }
        }
    }
</script>


<style lang="less">

    @import "../../assets/common.less";

    .container {
        padding-left: 0.30rem;
        .ok {
            background-color: #9c79b1;
            border-radius: 0.47rem;
            position: absolute;
            margin-top: 1.40rem;
            width: 6.82rem;
            height: 0.94rem;
            font-size: 0.32rem;
            font-weight: normal;
            font-stretch: normal;
            letter-spacing: 0px;
            color: #ffffff;
            line-height: 0.94rem;
        }
    }

</style>
